import React from "react";
import Sidebar from "./Sidebar";
import Toolbar from "./Toolbar";
import Main from "./Main";
import { BrowserRouter as Router } from "react-router-dom";

const App = () => {
  return (
    <Router>
      <div>
        <Toolbar />
        <Sidebar />
        <Main />
      </div>
    </Router>
  );
};

export default App;



