import React from "react";
import { Link } from "react-router-dom";

const EditBtn = props => {
  const { deckId } = props.match.params;
  return (
    <span>
      <Link className="btn" to={`/deck/${deckId}/new`}>
        + New Card
      </Link>
      <Link className="btn" to={`/deck/${deckId}/study`}>
        Study Deck
      </Link>
    </span>
  );
};

export default EditBtn;



